import CodeMirror from "@uiw/react-codemirror";
import { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
const END_POINT = "https://someerfanapp.azurewebsites.net/api/v1/wave";

function CodeForm(props) {
  const [code, setCode] = useState(
    `%%%%%%%%%%%%%%%%%%%%%%%%%%%%%
    %% Template for a SPARC file
    %% Author: 
    %% Description:
    %%%%%%%%%%%%%%%%%%%%%%%%%%%%%
    
    sorts
        #people = {sara, bob}.
    predicates
        father(#people, #people).
    rules
        father(bob, sara).
    `
  );

  const submit = () => {
    props.setLoading(true);
    fetch(END_POINT, {
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        action: "getQuery",
        query: "code",
        editor: code,
      }),
      method: "POST",
    })
      .then((response) => {
        if (!response.ok) {
          return response
            .json()
            .catch(() => {
              // Couldn't parse the JSON
              throw new Error(response.status);
            })
            .then(({ message }) => {
              // Got valid JSON with error response, use it
              throw new Error(message || response.status);
            });
        }
        return response.json();
      })
      .then((data) => {
        props.setData(data.result);
        console.log(data);
        props.setLoading(false);
      })
      .catch((d) => {
        props.setData(`${d}`);

        props.setLoading(false);
      });
  };

  return (
    <Form className="p-3">
      <h5 className="font-monospace">Enter your Question here</h5>
      <CodeMirror
        value={code}
        height="70vh"
        options={{
          theme: "dracula",
        }}
        onChange={(val) => setCode(val)}
      />
      <Button
        variant="primary"
        className="m-2 float-end"
        disabled={props.loading}
        onClick={!props.loading ? submit : null}
      >
        {props.loading ? "Processing..." : "Submit"}
      </Button>
    </Form>
  );
}

export default CodeForm;
